@import url(https://fonts.googleapis.com/css2?family=Heebo:wght@300;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


body{font-family:'Heebo', sans-serif;font-size:300}p{line-height:24px}img{width:100%;display:block}header{position:relative}#productos{padding:80px 0 0 0;display:grid;grid-gap:20px;gap:20px;width:60%;margin:0 auto;grid-template-columns:repeat(3, 1fr)}#productos li{text-align:center;background:#f1f1f1}#productos li h3{font-size:20px;padding:6px 0}#productos li *{margin-bottom:14px}#productos li p{background-color:#d3001c;color:white;padding:10px 22px;text-align:center;display:inline-block}#productos button{background-color:#4caf50;border:none;color:white;padding:10px 22px;text-align:center;text-decoration:none;display:inline-block;font-size:16px;cursor:pointer}#miTabla{font-family:"Trebuchet MS", Arial, Helvetica, sans-serif;border-collapse:collapse;width:100%}#miTabla td,#miTabla th{border:1px solid #ddd;padding:8px;overflow:hidden;height:1px}#miTabla td svg,#miTabla th svg{cursor:pointer}#miTabla tr:nth-child(even){background-color:#f2f2f2}#miTabla tr:hover{background-color:#bd0000}#miTabla th{padding-top:12px;padding-bottom:12px;text-align:left;background-color:#4CAF50;color:white;font-weight:700}@media only screen and (max-width: 1000px){#productos{width:80%}}@media only screen and (max-width: 600px){#productos{width:90%;grid-template-columns:repeat(2, 1fr)}}

